.ProjectsContainer{
  display: flex;
  flex-direction: column;
  padding-bottom: 70px;
  margin-left: 10vw;
  margin-right: 10vw;
}

.ProjectTag {
  display: flex;
  font-family: 'Archivo Narrow', sans-serif;
  letter-spacing: 0.7mm;
  font-weight: 500;
  font-size: 36px;
  justify-content: flex-start;
}

.ProjectDivider{
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 40px;
}




.ProjectImage{
  height: 250px;
  width: 400px;
}

.ProjectTitle{
  font-family: 'Archivo Narrow', sans-serif;
  letter-spacing: 0.7mm;
  font-size: 28px;
  margin-bottom: 5px;
}

.ProjectRole{
  font-family: 'Archivo Narrow', sans-serif;
  letter-spacing: 0.7mm;
  font-size: 18px;
  margin-top: 0px;
}

.ProjectLinks{ 
  display: flex;
  flex-direction: row;
  list-style-type: none;
  font-family: 'Asap Condensed', sans-serif;
  padding-inline-start: 0%;
}

.ProjectDescription{
  font-family: 'Asap Condensed', sans-serif;
  font-size: 16px;
  line-height: 22px;
}

.ProjectButton{
  background-color: var(--p3);
  border-radius: 12px;
  border-style: none;
  height: 30px;
  margin-right: 30px;
  padding-left: 10px;
  padding-right: 10px;
}

.ProjectText{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-right: 30px;
}

.ProjectLink{
  color: var(--tc);
  text-decoration: none;
}


@media (max-width: 786px){
  .ProjectContainer{
    border-style: double;
    border-color: var(--p1);
    border-top: none;
    border-left: none;
    border-right: none;
    padding-bottom: 30px;
  }
  .ProjectDivider{
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
  }
  .ProjectImage{
    display: flex;
    width: 85%;
    height: auto;
    padding-top: 30px;
  }
}